@charset "UTF-8";
@tailwind base;
@tailwind components;
@tailwind utilities;
/* ========BASE ============== */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header {
    margin: 0 auto;
}

.nav {
    padding: 0px 15%;
}

.links {
    background-color: transparent;
    color: #64748b;
    display: flex;
    font-family: Inter;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: -0.35px;
    line-height: 20px;
    padding: 6px 16px;
}
.links:hover {
    border-radius: 6px;
    outline: 1px solid #64748b;
    color: #2f3742;
    cursor: pointer;
}

.button-contactme {
    background-color: #ffffff;
    border-color: #94a3b8;
    border-radius: 6px;
    border-style: solid;
    border-width: 2px;
    color: #2f3742;
    display: flex;
    font-family: Inter;
    font-size: 13px;
    font-weight: 500;
    letter-spacing: -0.35px;
    line-height: 20px;
    margin: 0px 0px 0px 10px;
    padding: 6px 16px;
    transition: box-shadow 0.2s;
}
.button-contactme:hover {
    color: #0f172a;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}

/* ======== INTRO ============== */
.inter {
    font-family: Inter;
}

.primary {
    color: rgb(4, 120, 179);
}

.bg-primary {
    background-color: rgb(17, 124, 177);
}

.secondary {
    color: rgb(100, 116, 139);
}

.three {
    background-color: #0f172a;
}

.primaryButtonh:hover {
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
}

@media (min-width: 640px) {
    .widthDiv {
        width: 48rem;
        height: 24rem;
        padding: 0px 15%;
    }
}
@media (min-width: 1280px) {
    .widthDiv {
        width: 32rem;
        height: 24rem;
    }
}
